.works{
    background-color:crimson;
    display: flex;
    align-items: center;
    justify-content: center;

    .slider{
        height: 350px;
        display: flex;
        position: absolute;
        left: 0;
        
        .container{
            width: 100vw;
            display: flex;
            align-items: center;
            justify-content: center;

            .item{
                width: 700px;
                height: 100%;
                background-color: white;
                border-radius: 20px;
                display: flex;
                align-items: center;
                justify-content: center;

                .left{
                    flex:4 ;
                    height: 80%;
                    display: flex;
                    align-items: center;
                    justify-content: center;

                    .leftContainer{
                        width: 30%;
                        height: 20%;
                        display: flex;
                        flex-direction: column;
                        justify-content: space-between;

                        .imgContainer{
                            width: 20px;
                            height: 20px;
                            border-radius: 50%;
                            background-color: coral;
                            display: flex;
                            align-items: center;
                            justify-content: center;

                            img{
                                width: 25px;
                            }

                        }
                       
                    }

                }
                .right{
                    flex:8;
                    height: 100%;
                }
              
            }
        }
    }
}